body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* FLEX */
.flex { display: -webkit-flex; display: flex; }
.horizontal, .horizontal-only { -webkit-flex-direction: row; flex-direction: row; }
.vertical, .vertical-reverse { -webkit-flex-direction: column; flex-direction: column; }
@media screen and (max-width: 720px){
  .vertical-reverse { -webkit-flex-direction: row; flex-direction: row; }
  .horizontal { -webkit-flex-direction: column; flex-direction: column; }
}
.flex-1 { -webkit-flex: 1 1; flex: 1 1; }
.flex-2 { -webkit-flex: 2 1; flex: 2 1; }
.flex-3 { -webkit-flex: 3 1; flex: 3 1; }
.flex-4 { -webkit-flex: 4 1; flex: 4 1; }
.flex-5 { -webkit-flex: 5 1; flex: 5 1; }
.flex-6 { -webkit-flex: 6 1; flex: 6 1; }

.flex-center { -webkit-justify-content: center; justify-content: center; -webkit-align-items: center; align-items: center; }
.flex-end { -webkit-justify-content: flex-end; justify-content: flex-end; -webkit-align-items: center; align-items: center; }
.flex-start { -webkit-justify-content: flex-start; justify-content: flex-start; -webkit-align-items: center; align-items: center; }

.container, .container-big {
  padding: 10px;
  max-width: 1100px;
  width: calc(100%-20px);
  margin: 0 auto;
}
.container-big { max-width: 1440px; }
.text-center { text-align: center; }

/* COMPONENTS */
.TopBar {
  background-color: rgba(0, 0, 0, 0.859);
  color: #FEFEFE;
  font-size: 0.75rem; }
.TopBar .item {
  cursor: pointer; }
.TopBar .item i {
  font-size: 1rem; }
.TopBar .item:hover, .TopBar .item i {
  color: #2BC80B; }
.TopBar .item span {
  padding: 0 10px; }

.itens-header {
  background-color: #FEFEFE;
  margin: 0 10px 0 25px;
  border-radius: 20px;
  font-size: 0.9rem;
  height: 40px;
  width: 90%;
  cursor: pointer; }
.logo { 
  width: 85%;
  cursor: pointer; }
.input-pesquisa {
  width: 90%;
  margin-left: 30px;
  padding: 5px 15px;
  height: 30px;
  font-size: 0.9rem;
  border-radius: 20px;
  border: 0; }
.btn-pesquisa {
  margin-left: -40px;
  background-color: transparent;
  border: 0;
  cursor: pointer; }
.btn-pesquisa i {
  font-size: 1.3rem;
  color: #0E7742; }

.Categorias {
  background-color: #006B28;
  padding: 0; }
.cateogorias {
  color: #FCFCFC;
  font-weight: bold;
  font-size: 0.8rem; }
.categoria-item {
  cursor: pointer; }
.categoria-item:hover {
  color: #2BC80B; }

.cart-number {
  background-color: #0E7742;
  color: #FCFCFC;
  border-radius: 50px;
  width: 13px;
  padding-left: 5px;  margin-left: -10px; }
.aviso-wpp {
  background-color: #0E7742;
  font-size: 0.8rem;
  color: #FcFcFc;
  border-radius: 30px;
  padding: 0 8px; }

.breadcrumbs {
  color: #555;
  font-size: 0.9rem;
  padding: 20px 0; }

.main-pic-container {
  min-height: 450px; }
.main-pic {
  max-width: 100%;
  padding: 0 20px; }
.mini-pics-container {
  padding: 10px 0; }
.mini-pic-container {
  max-width: 100px;
  cursor: pointer;
  border: 1px solid #aaa;
  margin: 10px;
  height: 100%; }

.detalhes {
  padding: 40px 25px;
  box-shadow: 0 0 7px #ccc; }

.produto-detalhes {
  padding-bottom: 20px; }
.produto-detalhes h2 {
  margin: 0 0 5px 0; }
.produto-detalhes small {
  color: #666; }

.precos h2 { 
  font-size: 2rem;
  margin-bottom: 3px; }
.precos h2 small {
  font-size: 1rem;
  color: #666;
  font-weight: 300; }
.precos span {
  color: #666;
  padding-bottom: 30px;
  font-size: 0.9rem; }

.opcao label {
  color: #333;
  font-size: 0.95rem;
  font-weight: bold; }
.opcao input {
  padding: 5px 8px;
  font-size: 1rem;
  width: 50px; }

.btn-cta {
  padding: 20px 30px;
  color: #FEFEFE;
  background-color: #b50000;
  font-size: 1.2rem;
  font-weight: bold;
  margin-top: 20px;
  border: 0;
  cursor: pointer; }
.btn-cta:hover {
  background-color: #940909; }
.ctas { margin-bottom: 20px; }
.ctas i { font-size: 0.7rem; }
.cats small { font-size: 0.85rem; }

.cep-container h4 {
  margin-bottom: 8px; }
.cep-container input {
  border-radius: 10px;
  border: 1px solid #aaa;
  margin: 0 10px;
  padding: 5px 10px;
  font-size: 0.95rem; }
.btn-cep {
  background-color: #444;
  color: #FEFEFE;
  border: 0;
  border-radius: 10px;
  font-size: 1rem;
  padding: 5px 15px;
  cursor: pointer; }
.btn-cep:hover {
  background-color: #666; }

.hr { 
  width: 100%;
  background-color: #aaa;
  height: 1px;
  margin-top: 10px; }

.Descricao h2 {
  color: #222; }
.Descricao span, .Descricao li {
  color: #303030; }

.comentario {
  box-shadow: 0 0 7px #ccc;
  padding: 15px;
  margin: 30px 20px 60px 20px;
  border-radius: 10px; }
.comentario .mensagem {
  min-height: 60px; }
.comentario .dados {
  font-size: 0.85rem;
  color: #777; }

.produtos {
  margin-bottom: 60px; }
.titulo-sub {
  margin: 10px 0 60px 0;
  font-size: 1.85rem; }
.produto { 
  cursor: pointer; 
  padding: 10px 15px 50px 15px;
  margin: 0 10px;
  box-shadow: 0 0 5px #eee;
  max-width: 20%; }
.produto:hover { 
  box-shadow: 0 0 5px #777; }
.produto .produto-imagem {
  height: 100%;
  min-height: 300px; }
.produto .produto-imagem img {
  width: 80%; }
.produto-titulo {
  text-align: center;
  padding: 0 5px;
  color: #373737; }
.produto-precos h3 {
  text-align: center; 
  font-size: 1.4rem;
  margin: 0px 0 2px 0; }
.produto-precos span {
  text-align: center; 
  font-size: 0.85rem;
  color: #474747; }

.Institucional {
  padding: 40px 0; }
.institucional-item {
  padding: 0 10px; }
.institucional-item h3 { 
  color: #006B28;
  font-size: 1.2rem;
  font-weight: 700; }
.institucional-item span {
  padding: 0;
  font-size: 0.8rem;
  color: #444;
  cursor: pointer; } 
.institucional-item span:hover {
  text-decoration: underline; }
.institucional-item i {
  font-size: 1.4rem;
  padding: 10px;
  color: rgb(27, 133, 5); }

.FormasDePagamento {
  /* background-color: rgb(69, 37, 2); */
  padding: 10px; }
.cartao {
  padding: 0 3px; }

.Footer {
  background-color: rgb(255, 254, 253);
  color: rgb(25, 100, 11); }

.no-mb { display: -webkit-flex; display: flex; }
.no-mb-block { display: block; }
.only-mb { display: none; }
@media screen and (max-width: 720px){
  .produto { max-width: 100%; }
  .overflow-x { overflow-x: scroll; }

  .logo { width: 60%; }

  .no-mb { display: none; }
  .only-mb { display: -webkit-flex; display: flex; }
  .no-mb-block { display: none; }

  .input-pesquisa {
    width: 100%;
    margin: 10px 0 0 0; }

  .comentario { margin: 10px 0; }

  .btn-pesquisa i { margin-top: 10px; }

  .cep-container input { width: 100px; }
  
  .logo { width: 90%; }
}
